import { MoreVertical } from "lucide-react";

export default function InventoryPage() {
  const inventory = [
    { id: 1, name: "Threadix T-Shirt", category: "Clothing", stock: 120, status: "In Stock" },
    { id: 2, name: "Threadix Hoodie", category: "Clothing", stock: 5, status: "Low Stock" },
    { id: 3, name: "Threadix Mug", category: "Accessories", stock: 0, status: "Out of Stock" },
  ];

  return (
    <div className="p-8 flex-1">

      {/* PAGE HEADER */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-10">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Inventory</h1>
          <p className="text-gray-500 mt-1">Manage product stock and availability in Threadix store.</p>
        </div>

        <button className="px-5 py-2.5 bg-black text-white font-medium rounded-xl hover:bg-gray-900 transition">
          + Add Product
        </button>
      </div>

      {/* SEARCH + FILTERS */}
      <div className="bg-white p-5 rounded-xl border shadow-sm mb-8 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <input
          type="text"
          placeholder="Search inventory..."
          className="w-full md:w-72 px-4 py-2.5 border rounded-xl focus:ring-2 focus:ring-black outline-none transition"
        />

        <div className="flex gap-3">
          <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
            <option value="">All Categories</option>
            <option>Clothing</option>
            <option>Accessories</option>
          </select>

          <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
            <option value="">Stock Status</option>
            <option>In Stock</option>
            <option>Low Stock</option>
            <option>Out of Stock</option>
          </select>
        </div>
      </div>

      {/* INVENTORY TABLE */}
      <div className="bg-white border rounded-xl shadow-sm overflow-x-auto">
        <table className="w-full min-w-[700px]">
          <thead className="bg-gray-50 border-b">
            <tr>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Product</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Category</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Stock</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Status</th>
              <th className="text-right px-6 py-3 text-sm font-medium text-gray-600">Actions</th>
            </tr>
          </thead>

          <tbody>
            {inventory.map((item) => (
              <tr key={item.id} className="border-b last:border-none hover:bg-gray-50 transition">
                <td className="px-6 py-4 font-semibold text-gray-900">{item.name}</td>
                <td className="px-6 py-4 text-gray-700">{item.category}</td>
                <td className="px-6 py-4 text-gray-700">{item.stock}</td>
                <td className="px-6 py-4">
                  <span
                    className={`px-2.5 py-1 text-xs font-medium rounded-lg ${
                      item.status === "In Stock"
                        ? "bg-green-100 text-green-700"
                        : item.status === "Low Stock"
                        ? "bg-yellow-100 text-yellow-700"
                        : "bg-red-100 text-red-700"
                    }`}
                  >
                    {item.status}
                  </span>
                </td>
                <td className="px-6 py-4 text-right">
                  <button className="p-2 hover:bg-gray-200 rounded-full transition">
                    <MoreVertical size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

    </div>
  );
}
